import styled from 'styled-components';



const Form = styled.form`
  
.userSignInHeader {
  margin-bottom: 5px;
  font-size:26px;
  text-align: center;
}
.loginPage {
  margin: 75px auto;
  width: 250px;
  display:block;
}
 .login-form {
  border: solid #8FCFDE 2px;
  font-size: 24px;
  background-color: white;
  border-radius: 5px;
  padding:20px;
  padding-right:0px;
} 
.signInLabel {
  margin:50px;
  padding:2px;
}
.form-input {
  padding: 2px;
}
.signInButton {
  margin-top:10px;
  width:200px;
  background-color:#8FCFDE;
  border-radius:5px;
  color:white;
  height:35px;
}
.signUpPasswordConfirmLabel {
  padding:5px;
  margin:50px;
}
.signInButton:hover {
  color:#8FCFDE;
  background-color:#454545;
}
`;

export default Form;


